.box-news__item {
  height: 675px;
  position: relative;
  overflow: hidden;
}

@media screen and (max-width: 767px) {
  .box-news__item {
    height: 630px;
    margin-bottom: 28px;
  }
}

@media screen and (min-width: 768px) {
  .box-news__item {
    width: calc(50% - 15px);
  }
}

