.box-news__section {
  min-width: 112px;
  text-align: center;
  color: #fff;
  background: rgba(68, 64, 247, .7);
  border-radius: 0 4px 4px 0;
  padding: 4px 8px;
  font-family: Manrope;
  font-size: 12px;
