  opacity: 1;
  z-index: 9;
  background-color: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  gap: 4px;
  display: flex;
  position: absolute;
  top: 14px;
  right: 14px;
}

@media screen and (min-width: 768px) {
  li.show .box-news___wrap-reading {
    top: 20px;
    right: 20px;
  }
}

.box-news__reading {
