li.show .overlay {
  pointer-events: none;
  transform: translateX(0);
}

.overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(244, 244, 244, .4);
  transition: transform .25s;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

